@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&family=Roboto:wght@400;700&display=swap');

body {
  font-family: 'Noto Sans', 'Roboto', Arial, Helvetica, sans-serif;
  font-size: 1.08rem;
  color: #222;
  background: #fafbfc;
  line-height: 1.7;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto', 'Noto Sans', Arial, Helvetica, sans-serif;
  font-weight: 700;
  letter-spacing: 0.01em;
  color: #1a1a1a;
}

pre,
code,
pre *,
code * {
  font-family: 'Fira Mono', 'Menlo', 'Monaco', 'Consolas', 'Liberation Mono', 'Courier New', monospace;
}

// inline code
code.highlighter-rouge {
  padding: 2px 6px;
  background: var(--light-gray);
  border-radius: var(--rounded);
}

// code block
div.highlighter-rouge {
  width: 100%;
  margin: 40px 0;
  border-radius: var(--rounded);
  overflow-x: auto;
  overflow-y: auto;
  text-align: left;

  div.highlight {
    display: contents;

    pre.highlight {
      width: fit-content;
      min-width: 100%;
      margin: 0;
      padding: 20px;
      color: var(--white);
    }
  }
}
